import * as React from 'react';
import { Alert, Typography } from "@material-tailwind/react";
import Header from './components/Header';
import FormContent from './components/FormContent';
import Skeleton from '../../../containers/src/components/portal-layouts/Skeleton';
import warningIcon from './assets/icons/warning.png';

import './application_form.scss';

function App() {
  const inValids = JSON.parse(localStorage.getItem('invalid_data') || '[]');

  return (
    <React.Suspense fallback={<Skeleton />}>
      <div className="bg-sky-50">
        <div className="pl-14 py-5 pr-5 mx-10">
          <Header />
          {inValids.length ?
            <Alert className="my-2 bg-[#fff6e7] border-2 border-[#ff7a00]">
              <div className="flex items-center gap-2">
                <img src={warningIcon} alt="Warning icon" />
                {inValids.map((item) =>
                  <Typography variant="small" className="text-black">{item.ps_message}</Typography>
                )}
              </div>
            </Alert> :
            null}
          <FormContent />
        </div>
      </div>
    </React.Suspense >
  );
};

export default App;